          transform: rotate(90deg);
}

.horizontal-ruler hr:nth-child(21) { -webkit-transform: rotate(90deg); transform: rotate(90deg); }
.horizontal-ruler hr:nth-child(22) { -webkit-transform: rotate(90deg); transform: rotate(90deg); }
.horizontal-ruler hr:nth-child(23) { -webkit-transform: rotate(90deg); transform: rotate(90deg); }
.horizontal-ruler hr:nth-child(24) { -webkit-transform: rotate(90deg); transform: rotate(90deg); }
.horizontal-ruler hr:nth-child(25) {
  width: 15px;
