#custom-toast {
  position: relative;
  opacity: 1;
  background-color: rgba(255, 255, 255, 1);
  -webkit-transform: translateX(calc(100% + 25px));
          transform: translateX(calc(100% + 25px));
  transition: height 0.2s linear, border-width 0.2s linear, padding 0.2s linear, -webkit-transform 0.3s linear;
  transition: transform 0.3s linear, height 0.2s linear, border-width 0.2s linear, padding 0.2s linear;
  transition: transform 0.3s linear, height 0.2s linear, border-width 0.2s linear, padding 0.2s linear, -webkit-transform 0.3s linear;
  overflow:hidden;
}

#custom-toast.show { -webkit-transform: translateX(0); transform: translateX(0); }

#custom-toast.hide {
  display:block;
  opacity: 1;
  transition: -webkit-transform 0.3s linear;
  transition: transform 0.3s linear;
  transition: transform 0.3s linear, -webkit-transform 0.3s linear;
}

#custom-toast .btn-close:focus,
#custom-toast .btn-close:active {
  box-shadow: none;
}
